.header {
    min-height: 100vh;
    height: auto;
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    padding-bottom: 10rem;
    background-image: radial-gradient(fade(@color-4, 85%) 5%, darken(@color-4, 15%) 85%), url('../img/header-bg.jpg');
    background-size: cover;
    background-position: center bottom;
    background-attachment: fixed;
    &::before,
    &::after {
        content: '';
        position: absolute;
        left: -100%;
        right: -100%;
        height: 100rem;
        bottom: -100rem;
        background-color: lighten(@color-1, 12%);
        z-index: 200;
    }
    &::before {
        transform: rotate(6deg);
    }
    &::after {
        transform: rotate(-6deg);
    }
    /* header end */
    &-navbar {
        padding: 1rem;
        border-bottom: 1.5px solid fade(#ffffff, 25%);
        margin-bottom: 2em;
        text-align: left;
    }
    .title(#fff,
        {
        overflow: hidden;
        animation-name: anim-title-container;
        animation-duration: 0.5s;
        animation-delay: 2s;
        animation-fill-mode: backwards;
        margin-top: auto;
        margin-bottom: auto;
        &::before {
            // verticle bar
            content: '';
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            height: 100%;
            width: 3px;
            background-color: #ffffff;
            animation: anim-pulse 2s 0.3s both alternate;
            .media(@xs, {
                animation: both;
                display: none;
            }
            );
        }
    }
    );
    .container {
        position: relative;
        z-index: 100;
        padding-top: 2em;
        margin-top: 3em;
        font-size: 1.4rem;
        .media(@sm,
            {
            display: none;
        }
        );
        &::after {
            content: '';
            position: absolute;
            left: -100%;
            right: -100%;
            bottom: -300%;
            top: 0;
            background-image: linear-gradient(to bottom, fade(@color-4, 25%) 5%, darken(@color-4, 15%) 85%);
            z-index: -1;
        }
    }
    &-target {
        .col-grow();
        color: #ffffff;
        text-align: center;
        font-size: inherit;
        position: relative;
        .media(@md,
            {
            line-height: 1.2;
        }
        );
        &:not(:last-child)::after {
            content: '';
            position: absolute;
            right: -15px;
            top: 10%;
            bottom: 10%;
            border-right: 1.5px solid fade(#fff, 25%);
        }
        p {
            font-size: 1.2em;
            margin-bottom: .5em;
            font-weight: bold;
        }
    }
    /* animation */
    &-title,
    &-text {
        animation-duration: 0.5s;
        animation-delay: 2.5s;
        animation-fill-mode: backwards;
        animation-name: anim-title-content;
        line-height: 1.4;
        .media(@xs,
            {
            animation: both;
        }
        );
    }
    /* animation end */
    &-title {
        font-size: 5rem;
        text-transform: uppercase;
        .media(@md,
            {
            font-size: 3.5rem;
        }
        );
        .media(@xs,
            {
            font-size: 2.5rem;
        }
        );
    }
    &-text {
        font-size: 2rem;
        .media(@md,
            {
            font-size: 1.6rem;
        }
        );
    }
    &-icon {
        position: absolute;
        bottom: 4rem;
        left: 50%;
        font-size: 3rem;
        color: #ffffff;
        margin-left: -19px / 2;
        z-index: 200;
        .media(@xs,
            {
            bottom: 10rem;
        }
        );
        i {
            cursor: pointer;
        }
    }
}