.btn,
a.btn,
[type=button].btn,
[type=submit].btn {
    display: inline-block;
    padding: 0;
    margin: 0;
    line-height: 1;
    border: none;
    text-decoration: none;
    box-shadow: none;
    transition: all .3s;
    color: inherit;
    &-sm {
        font-size: 1.4rem;
    }
    &-main {
        background-color: @color-4;
        color: #ffffff;
        border-radius: 5px;
        padding: 1em;
        .bxsh(@color-4);
        &:hover,
        &:focus {
            background-color: lighten(@color-4, 10%);
            box-shadow: none;
        }
    }
}