*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    font-size: 10px;
}

body {
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-size: 16px;
    line-height: 1.4;
    background-color: transparent;
    color: @color-text;
}

a {
    text-decoration: none;
}

h1,
h2,
h3,
h4,
p,
ul,
ol,
blockquote {
    padding: 0;
    margin: 0;
}

ul,
ol {
    list-style-type: none;
}

strong {
    font-size: 1.1em;
}

.container {
    .wrapper();
}

.row {
    .row();
}

.logo-link {
    font-size: 20px;
    font-weight: 700;
    letter-spacing: 1px;
    color: #ffffff;
    text-decoration: none;
}

.title-hidden {
    visibility: hidden;
    overflow: hidden;
    height: 0;
}

.icon {
    font-family: 'Icon';
    .fa-icon();
    font-weight: 900;
}

.angle-double-down {
    animation: anim-bounce 2s infinite ease-in-out;
    .icon-create(@fa-var-angle-double-down);
}

.html5 {
    .icon-create(@fa-var-html5);
    color: #ff4c1e;
}

.css3-alt {
    .icon-create(@fa-var-css3-alt);
    color: #009cd9;
}

.js-square {
    .icon-create(@fa-var-js-square);
    color: #f0db4f;
}

.user-graduate {
    .icon-create(@fa-var-user-graduate);
}