@font-path: '../fonts/';

@font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 100;
    src: url('@{font-path}/roboto-v18-cyrillic_latin-100.eot');
    /* IE9 Compat Modes */
    src: local('Roboto Thin'), local('Roboto-Thin'), url('@{font-path}/roboto-v18-cyrillic_latin-100.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('@{font-path}/roboto-v18-cyrillic_latin-100.woff2') format('woff2'), /* Super Modern Browsers */
    url('@{font-path}/roboto-v18-cyrillic_latin-100.woff') format('woff');
    /* Modern Browsers */
}

