.main {
    padding-top: 8rem;
    padding-bottom: 8rem;
    background-color: lighten(@color-1, 12%);
}

.content {
    font-size: 1.8rem;
    line-height: 1.7;
    &:not(:last-child) {
        margin-bottom: 4em;
    }
    .media(@md, {
        font-size: 1.6rem;
        line-height: 1.5;
    }
    );
    .title();
    &-title {
        font-size: 1.7em;
    }
    &-subtitle {
        text-align: left;
        font-size: 1.2em;
        margin-bottom: 1em;
        font-weight: bold;
        &::after {
            content: '';
            display: block;
            width: 50px;
            border-bottom: 2px solid @color-text;
        }
        /*  */
        &-center {
            text-align: center;
            &::after {
                margin-right: auto;
                margin-left: auto;
            }
        }
        /*  */
        &-white {
            color: #ffffff;
            &::after {
                border-color: #ffffff;
            }
        }
    }
    &-text {
        .col(10);
        .col-lg();
        &:not(:last-child),
        &:first-child {
            margin-bottom: 1.5em;
        }
    }
    .container {
        background-color: #ffffff;
        position: relative;
        z-index: 10;
        .card(@color-1);
    }
    &-quote {
        .col();
        .card(@color-3);
        background-color: lighten(@color-3, 10%);
        color: #ffffff;
        overflow: hidden;
        text-align: center;
        position: relative;
        z-index: 10;
        font-size: .9em;
        &:not(:last-child) {
            margin-bottom: 1.5em;
        }
        small {
            display: block;
            margin-top: 1em;
            font-style: italic;
        }
        .icon-create(@fa-var-quote-left, {
            font-size: 7em;
            opacity: 0.05;
            position: absolute;
            top: 50%;
            margin-top: -1em / 2;
            left: 15px;
            z-index: -1;
        }
        );
    }
    &-card {
        .col();
        .card(@color-4);
        background-color: @color-4;
        color: #ffffff;
        text-align: center;
        margin-bottom: 1.5em;
    }
}

.level {
    &-body {
        .col(10);
        .col-lg();
        position: relative;
        margin-bottom: 1.5em;
    }
    &-range {
        height: 3px;
        align-self: flex-end;
        border-radius: 2px;
        background-image: linear-gradient(to right, #4CAF50, #FFEB3B, #d50000);
        margin-top: 5px;
        box-shadow: 1px 1px 2px fade(#b71c1c, 50%);
    }
    &-range-50 {
        max-width: 50%;
    }
    &-title {
        display: inline-block;
        font-size: .8em;
        opacity: 0.7;
    }
    .icon {
        margin-right: 10px;
        font-size: 2em;
        text-shadow: 1px 1px 1px fade(#000, 20%);
    }
    strong {
        display: inline-block;
        min-width: 8rem;
    }
}

.course {
    &-link {
        display: block;
        position: relative;
        z-index: 100;
        text-align: center;
        background-color: @color-4;
        color: #ffffff;
        transition: 0.7s;
        .card(@color-1, 10px);
        &.active {
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }
        &::before {
            content: '';
            position: absolute;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #ffffff;
            left: 15px;
            top: 50%;
            margin-top: -10px / 2;
        }
        &:hover {
            background-color: lighten(@color-4, 10%);
        }
    }
    &-text {
        .card();
        height: 100%;
        border-top-right-radius: 0;
        border-top-left-radius: 0;
        z-index: 10;
        position: relative;
        text-align: center;
        overflow: hidden;
        &:not(.active) {
            display: none;
        }
        /* icon */
        &::before {
            font-size: 7em;
            opacity: 0.05;
            position: absolute;
            top: 50%;
            margin-top: -1em / 2;
            right: 15px;
            z-index: -1;
        }
        /* icon end */
    }
    &-list {
        .col();
        li {
            &:not(:last-child) {
                margin-bottom: 1.5em;
            }
            /* icon */
            &:nth-of-type(1) .course-text {
                .icon-create(@fa-var-php);
            }
            &:nth-of-type(2) .course-text {
                .icon-create(@fa-var-database);
            }
            &:nth-of-type(3) .course-text {
                .icon-create('\e922', {
                    font-weight: 400;
                }
                );
            }
            &:nth-of-type(4) .course-text {
                .icon-create('\e91f', {
                    font-weight: 400;
                }
                );
            }
            &:nth-of-type(5) .course-text {
                .icon-create(@fa-var-laravel);
            }
            &:nth-of-type(6) .course-text {
                .icon-create(@fa-var-laravel);
            }
            /* icon end */
        }
    }
}

.process {
    &-image {
        .card(@color-1, 0, 0);
        position: relative;
        padding-bottom: floor(100% / 1.9);
        overflow: hidden;
        &:not(:first-child) {
            margin-top: 1.5em;
        }
        margin-bottom: 1.5em;
        img {
            position: absolute;
            top: -10%;
            left: 0;
            width: 100%;
        }
    }
}

.open {
    &-list {
        .col();
        list-style-type: disc;
        list-style-position: inside;
    }
    &-btn {
        padding-top: 1.5em;
        .media(@xs, {
            text-align: center;
        });
    }
}